<template>
  <div class="print-inner">
    <h3 class="print-check-title">处方笺</h3>
    <patient-info :patient-data="patientData"/>
    <div class="print-chufang-box">
      <div class="chufang-left">
            <p>
              <span>医保提示:</span>
              <br>
              开药量急性病不得超过三日量，慢性病不超过七日量，行动不便的可开两周量。患高血压、糖尿病、冠心病、慢性肝炎、肝硬化、结核病、精神病、癌症、脑血管病、前列腺肥大疾病，且病情稳定需长期服用同一类药物的，可放宽到不超过一个月量
            </p>
      </div>
      <div class="chufang-right">
        <h3>Rp:</h3>
        <p class="chufang-item" v-for="(item,index) in fuangjiList" :key="index">
          <span> {{ item.drugName }} </span>
          <span> {{ item.quanitity }} {{ item.prescriptionUnitName }} </span>
          <span> 次数: {{ item.frequencyName }} x {{ item.useDays }} </span>
          <span> 用量: {{ item.singleMeasure }} {{ item.singleMeasureUnitName }} </span>
        </p>
      </div>
    </div>
    <div class="print-chufang-footer">
      <span>药品金额: </span>
      <span>计价员:</span>
      <span>审核/调配签名：</span>
      <span>核对/发药签名：</span>
      <span></span>
    </div>
    <div style="margin-top: 5px">
      药师提示： 1、请遵医嘱服药；2、请在窗口清点药品；3、处方当日在本院有效；4、发出药品不予退还
    </div>
  </div>
</template>

<script>
import patientInfo from '../../components/print-patientInfo'
import {
  getEmpBasicMainEntyTable,
  getFangJiTable,
  printChuFang,
  getWesternDrugByFangJiSubID,
  getYiCunMedicareCure
} from '../../api'
export default {
  components: {
    patientInfo
  },
  data () {
    return {
      patientName: '',
      patientSex: '',
      patientAge: '',
      deptName: '',
      registrationNum: '',
      patientData: {},
      fuangjiList: [],
    }
  },
  computed: {

  },
  methods: {
    getPatintData () {
      getEmpBasicMainEntyTable({ registrationNum: this.registrationNum }).then(res=> {
        if(res.data.code == '0'){
          this.patientData = res.data.data[0]
          this.patientData.emSex = this.patientData.emSex === '1' ? '男' : '女'
          // 缺少西药字段
          // printChuFang( { empBasicMainID: this.patientData.empBasicMainID , drugFlag: 2 } ).then( res => {
          //   if (res.data.code == '0') {
          //     this.fuangjiList = res.data.data.fangJiDetailList
          //   }
          // })
          this.getFangJiData()
        }else{
          this.$message.error('获取患者信息失败！')
        }
      })
    },
    getFangJiData () {
      getFangJiTable({ empBasicMainID: this.patientData.empBasicMainID }).then(res => {
        if (res.data.code == '0')  {
          for (let index = 0; index < res.data.data.length; index++) {
            const element = res.data.data[index];
            this.getFangJiList( res.data.data[index].fangJiSubID )
          }
        }
      })
    },
    getFangJiList ( fangjiId ) {
      getWesternDrugByFangJiSubID({ empBasicMainID: this.patientData.empBasicMainID, fangJiSubID:  fangjiId}).then(res => {
        if (res.data.code == '0') {
          this.fuangjiList = [...this.fuangjiList,...res.data.data]
        }
      })
    }
  },
  mounted () {
    let id= this.$route.query.id
    if (id) {
        this.registrationNum = id
       this.getPatintData()
    }else{
      this.$message.error('访问参数不正确！')
    }
  }
}
</script>

<style>
.print-inner{
  width: 960px;
  padding-left: 40px;
}
.print-check-title{
  padding: 24px 0;
  width: 100%;
  font-size: 26px;
  font-weight: normal;
  color: #000;
  text-align: center;
}
.print-chufang-box {
  display: flex;
  width: 100%;
}
.print-chufang-box div{

}
.print-chufang-box .chufang-left{
  width: 30%;
  border-left:  1px solid #000;
}
.print-chufang-box .chufang-left p{
  text-align: justify;
  padding: 24px;
  line-height: 24px;
}
.print-chufang-box .chufang-left p span{
  font-weight: bold;
  line-height: 40px;
}
.print-chufang-box .chufang-right{
  width: 70%;
  border-left:  1px solid #000;
  border-right:  1px solid #000;
  padding: 20px;
}
.print-linzheng-box{
  padding: 0 10px;
}
.chufang-item {
  line-height: 26px;
}
.chufang-item span{
  margin-left: 30px;
}
.print-linzheng-box ul{
  width: 100%;
}
.print-linzheng-box ul li{
  float: left;
  width: 20%;
  line-height: 30px;
  text-align: center;
}
.print-chufang-footer{
  display: flex;
  width: 100%;
  padding: 10px 0;
  justify-content:space-between;
  border-top: 1px solid #000;
}
</style>



